import Bezier from 'bezier-js'
import Victor from 'victor'

import { CanvasRenderer } from './CanvasRenderer'
// import { handleInteraction } from './interaction'

function getT(curve, p, steps) {
  // find T for clicked point
  let bestT = undefined;
  let bestDist = undefined;

  const step = 1.0 / (steps || 50.0);

  for(var t=0.0; t<=1.0; t+=step) {
    const curP = curve.get(t);
    const dist = new Victor(p.x - curP.x, p.y - curP.y).length();

    if (bestT === undefined || dist < bestDist) {
      bestT = t;
      bestDist = dist;
    }
  }

  return bestT;
}

class BaseInteraction {
  constructor(canvas, curve, opts) {
    this.curve = curve;
    this.canvas = canvas;

    this.opts = opts || {}
    this.setup();
  }

  setup() {
    const down = (evt) => this._onMouseDown(evt);
    this.canvas.addEventListener('mousedown', down);
    const move = (evt) => this._onMouseMove(evt)
    this.canvas.addEventListener('mousemove', move);
    const up = (evt) => this._onMouseUp(evt)
    this.canvas.addEventListener('mouseup', up);
    const click = (evt) => this._onClick(evt);
    this.canvas.addEventListener('click', click);
    const dblclick = (evt) => this._onDoubleClick(evt);
    this.canvas.addEventListener('dblclick', dblclick);

    this.addCleanup(() => {
      this.canvas.removeEventListener('mousedown', down);
      this.canvas.removeEventListener('mousemove', move);
      this.canvas.removeEventListener('mouseup', up);
      this.canvas.removeEventListener('click', click);
      this.canvas.removeEventListener('dblclick', dblclick);
    });
  }

  cleanup() {
    if (this._cleanupFuncs) {
      this._cleanupFuncs.forEach(func => func());
      this._cleanupFuncs = undefined;
    }
  }

  addCleanup(func) {
    if (!this._cleanupFuncs) {
     this._cleanupFuncs = [];
    }

    this._cleanupFuncs.push(func);
  }

  onMouseDown() {}
  onMouseMove() {}
  onMouseUp() {}
  onClick() {}
  onDoubleClick() {}

  _onMouseDown(evt) {
    if (this.opts.mouseDownCallback) {
      this.opts.mouseDownCallback(evt);
    }
    this.onMouseDown(evt);
  }

  _onMouseMove(evt) {
    if (this.opts.mouseMoveCallback) {
      this.opts.mouseMoveCallback(evt);
    }
    this.onMouseMove();
  }

  _onMouseUp(evt) {
    if (this.opts.mouseUpCallback) {
      this.opts.mouseUpCallback(evt);
    }
    this.onMouseUp();
  }

  _onClick(evt) {
    if (this.opts.clickCallback) {
      this.opts.clickCallback(evt);
    }
    this.onClick();
  }

  _onDoubleClick(evt) {
    if (this.opts.doubleClickCallback) {
      this.opts.doubleClickCallback(evt);
    }
    this.onDoubleClick();
  }

  _getCanvasOffset(evt) {
    const e = evt || window.event;
    const rect = (e.target || e.srcElement).getBoundingClientRect();
    return {x: e.clientX - rect.left, y: e.clientY - rect.top};
  }
}

class CurveInteraction extends BaseInteraction {
  constructor(canvas, curve, opts) {
    super(canvas, curve, opts);
    this.renderer = new CanvasRenderer(canvas);

    opts = opts || {}
    this.curveThickness = (opts || {}).curveThickness || 5;
    this.curveHoverCallback = opts.curveHoverCallback;
    this.curveClickCallback = opts.curveClickCallback;
    this.curveClickAtCallback = opts.curveClickAtCallback;
    this.curveIsHovered = false;
  }

  foo() {}

  onMouseMove(evt) {
    const offset = this._getCanvasOffset(evt);
    const isHovered = this.isOverCurve(offset);

    if (isHovered === this.curveIsHovered) {
      return;
    }

    this.curveIsHovered = isHovered;
    if (this.curveHoverCallback) {
      this.curveHoverCallback(this.curveIsHovered);
    }
  }

  onClick(evt) {
    if (this.curveIsHovered) {
      if (this.curveClickCallback) {
        const offset = this._getCanvasOffset(evt);
        const p = this.curve.project({x: offset.x, y: offset.y});
        this.curveClickCallback(p.x, p.y);
      }

      if (this.curveClickAtCallback) {
        // clicked point
        const offset = this._getCanvasOffset(evt);
        const p = this.curve.project({x: offset.x, y: offset.y});

        // find T for clicked point
        this.curveClickAtCallback(getT(this.curve, p, 50));
      }
    }
  }

  isOverCurve(offset) {
    // get closest point ON curve
    const p = this.curve.project(offset);

    // get distance to point on curve
    const vector = new Victor(p.x - offset.x, p.y - offset.y);
    return vector.length() < this.curveThickness;
  }
}

class CurveHandlesInteraction extends BaseInteraction {
  handleRadius = 5;
  hoverPoint = null;
  dragPoint = null;
  dragPointStartPos = null;
  downPos = null;
  fixedPoints = [];

  constructor(canvas, curve, opts) {
    super(canvas, curve, opts);
    this.fixedPoints = opts.fixedPoints || [];
    this.opts = opts || {};
  }

  onMouseDown(evt) {
    this.downPos = this._getCanvasOffset(evt);

    if (this.hoverPoint) {
      // console.log('Start dragging point');
      this.dragPoint = this.hoverPoint;
      this.dragPointStartPos = {x : this.hoverPoint.x, y: this.hoverPoint.y};
    }
  }

  _pointForOffset(offset, radius) {
    radius = radius || this.handleRadius;
    return this.curve.points.find((p) => {
      const dist = new Victor(p.x, p.y).distance(new Victor(offset.x, offset.y));
      return dist < radius;
    });
  }

  onMouseMove(evt) {
    const offset = this._getCanvasOffset(evt);

    let point = this.dragPoint || this._pointForOffset(offset);

    if (point && this.fixedPoints) {
      if (this.fixedPoints.indexOf(point) !== -1) {
        point = undefined;
      }
    }

    if (point !== this.hoverPoint) {
      this.canvas.style.cursor = point === undefined ? 'default' : 'pointer';

      this.hoverPoint = point;

      if (this.opts.hoverPointCallback) {
        this.opts.hoverPointCallback(this.hoverPoint);
      }
    }
  
    if(this.dragPoint === null) {
      return;
    }

    const dragX = offset.x - this.downPos.x;
    const dragY = offset.y - this.downPos.y;
    this.dragPoint.x = this.dragPointStartPos.x + dragX;
    this.dragPoint.y = this.dragPointStartPos.y + dragY;
    this.curve.update();
    if (this.opts.updateCallback) {
      this.opts.updateCallback();
    }
  }

  onMouseUp() {
    if (this.dragPoint && this.opts.endDragCallback) {
      this.opts.endDragCallback(this, this.dragPoint);
    }

    this.dragPoint = null;
  }

  onDoubleClick() {
    if (this.hoverPoint === undefined) {
      return;
    }

    if (this.opts.pointDoubleClickCallback) {
      this.opts.pointDoubleClickCallback(this, this.hoverPoint);
    }

    if (this.opts.pointRemoveCallback) {
      // will select the corresponding endpoint when a handle point is clicked
      let p = this.hoverPoint;
      const idx = this.curve.points.indexOf(p);
      if (idx === 2) p = this.curve.points[3];
      if (idx === 1) p = this.curve.points[0];

      if (this.fixedPoints.indexOf(p)) { // TODO this check isn't working
        this.opts.pointRemoveCallback(this, p);
      }
    }
  }
}


function createCircleCurves(x, y, radius) {
  // https://stackoverflow.com/questions/1734745/how-to-create-circle-with-b%C3%A9zier-curves
  const handleLength = radius; // * 0.552284749831;

  return [
    // top half
    new Bezier([
      x-radius, y,
      x-radius, y-handleLength,
      x+radius, y-handleLength,
      x+radius, y
    ]),
    // bottom half
    new Bezier([
      x-radius, y,
      x-radius, y+handleLength,
      x+radius, y+handleLength,
      x+radius, y
    ])
  ];
}

function getInvalidCurves(curves, offsetAmount, boundingBox, drillbitRadius, opts) {
  const invalid = [];
  
  opts = opts || {};

  const makeInvalid = (c) => {
    if (invalid.indexOf(c) === -1) invalid.push(c);
  };

  const alreadyInvalid = (c) => invalid.indexOf(c) !== -1;

  // find curves that exceed the boundingBox
  curves.forEach((c) => {
    const bbox = c.bbox();
    if (bbox.x.min < boundingBox.x.min ||
      bbox.x.max > boundingBox.x.max ||
      bbox.y.min < boundingBox.y.min ||
      bbox.y.max > boundingBox.y.max) {
      makeInvalid(c);
    }
  });

  // // find curves whose offsets intersect
  // curves.forEach((c, idx) => {
  //   if (alreadyInvalid(c)) {
  //     return;
  //   }

  //   for (var j=0; j<idx; j++) {
  //     const prev = curves[j];

  //     const offsets1 = prev.offset(offsetAmount);
  //     const offsets2 = c.offset(offsetAmount);
     
  //     const intersecting = offsets1.find((o) => {
  //       return offsets2.find((o2) => {
  //         return o.intersects(o2).length > 0;
  //       }) !== undefined;
  //     });

  //     if (intersecting !== undefined) {
  //       makeInvalid(prev);
  //       makeInvalid(c);
  //     }
  //   }
  // });

  // find any point where the offset drillbit will touch the curve or any other curve
  curves.forEach((c) => {
    if (alreadyInvalid(c)) {
      return;
    }

    const step = 1.0 / 20.0;

    for(let t=0.0; t<=1.0; t+=step) {
      const p = c.offset(t, -drillbitRadius-0.5);
      const drillbitCurves = createCircleCurves(p.x, p.y, drillbitRadius);

      // loop over each curve in the drillbit circle
      const intersecting = drillbitCurves.find((dc) => {
        // find an intersection with any part of the skateboard curves
        return curves.find((cc) => {
          return dc.intersects(cc).length > 0;
        }) !== undefined;
      });

      if (intersecting !== undefined) {
        makeInvalid(c);
        if (opts.drillbitErrorsCallback) {
          opts.drillbitErrorsCallback(drillbitCurves);
        }
      }
    }
  });


  return invalid;
}

export class Curver {
  curves = [];
  curveInteractions = [];
  handlesInteractions = [];
  cleanupFuncs = [];

  hoveredPoint = undefined;
  fixedPoints = [];
  offsetRadius = -8;
  drillbitRadius = 5;
  validationDelay = 10; // ms
  validationTimeout = undefined;

  invalidCurves = [];
  drillbitErrors = [];

  boundingBox = {
    x: {min: 0, max: 100},
    y: {min: 0, max: 100}
  }

  constructor(canvas, curves) {
    this.canvas = canvas;

    const offset = {x: this.canvas.clientWidth / 2, y: this.canvas.clientHeight / 2};

    // start with 1 curve
    // curves = curves || [new Bezier(
    //   0+offset.x,-100+offset.y,
    //   50+offset.x,-70+offset.y,
    //   50+offset.x,70+offset.y,
    //   0+offset.x,100+offset.y)];

    // start with 3 curves
    curves = curves || [
      new Bezier(
        0+offset.x,-100+offset.y,
        50+offset.x,-100+offset.y,
        50+offset.x,-70+offset.y,
        50+offset.x,-50+offset.y),
      
      new Bezier(
        50+offset.x,-50+offset.y,
        51+offset.x,-20+offset.y,
        51+offset.x,20+offset.y,
        50+offset.x,50+offset.y,),
        
      new Bezier(
        50+offset.x,50+offset.y,
        50+offset.x,60+offset.y,
        50+offset.x,100+offset.y,
        0+offset.x,100+offset.y),
        
        ];
    
    this.boundingBox.x.min = offset.x - 75;
    this.boundingBox.x.max = offset.x + 75;
    this.boundingBox.y.min = offset.y - 120;
    this.boundingBox.y.max = offset.y + 120;

    this.setup(curves);
  }

  setup(curves) {
    this.renderer = new CanvasRenderer(this.canvas);

    if (curves) {
      this.fixedPoints = [
        curves[0].points[0],
        curves[curves.length-1].points[3]
      ];
      curves.forEach((c) => this.addCurve(c));
    }

    this.validate();
    this.draw();

    const draw = () => this.draw();
    const resize = () => this.onWindowResize();

    this.canvas.addEventListener('mousemove', draw);
    window.addEventListener('resize', resize);

    this.cleanupFuncs.push(() => {
      this.canvas.removeEventListener('mousemove', draw);
      window.removeEventListener('resize', resize);
    });
  }

  cleanup() {
    this.curves.forEach((c) => {
      this.removeCurve(c);
    });

    this.cleanupFuncs.forEach((f) => f());
    this.cleanupFuncs.length = 0;
  }

  draw() {
    const offset = {x:0, y:0};

    this.renderer.clear();

    // render bounding box
    this.renderer.setColor('#DDD');
    this.renderer.drawbbox(this.boundingBox);

    // render static 'mirrored' half     
    this.renderer.setColor('#555555');
    this._getMirrored(this.curves).forEach((c) => {
      this.renderer.drawCurve(c, offset);
    });

    // render editable half
    this.renderer.setColor('#000000');

    this.curves.forEach((c) => {
      this.renderer.drawSkeleton(c, offset);
      this.renderer.drawCurve(c, offset);
    });

    // render hovered point/curve
    if (this.hoveredPoint) {
      this.renderer.setColor('#00AA00', () => {
        this.renderer.drawPoints([this.hoveredPoint], offset);
      });
    }
    else {
      this.curveInteractions.forEach((int) => {
        if (int.curveIsHovered) {
          this.renderer.setColor('#00AA00', () => {
            this.renderer.setLineWidth(2, () => {
              this.renderer.drawCurve(int.curve, offset);
            });
          });
        }
      });
    }
    
    // render fixed points
    this.renderer.setColor('#DDDDDD', () => {
      this.renderer.drawPoints(this.fixedPoints, offset);
    });

    // render offset
    this.renderer.setColor('#EE0', () => {
      this.curves.forEach((cc) => {
        const offsetCurves = cc.offset(this.offsetRadius);
        // const offsetCurves = [cc.scale(-10)];
        offsetCurves.forEach((c) => this.renderer.drawCurve(c));
      });
    });

    // draw invalid curves in thick red
    this.renderer.setColor('#FF0000', () => {
      this.renderer.setLineWidth(3, () => {
        this.invalidCurves.forEach((c) => {
          this.renderer.drawCurve(c);
        });
      });
    });

    // draw drillbit errors in thinner red

    this.renderer.setColor('#FF00FF', () => {
      this.drillbitErrors.forEach((c) => {
        this.renderer.drawCurve(c);
      });
    });
  }

  validate() {
    this.drillbitErrors = []
    this.invalidCurves = getInvalidCurves(this.curves, this.offsetRadius, this.boundingBox, this.drillbitRadius, {
      drillbitErrorsCallback: (drillbitCurves) => {
        // console.log('got drillbit error:', drillbitCurves);
        drillbitCurves.forEach((c) => this.drillbitErrors.push(c));
      }
    });
  }

  addCurve(curve) {
    const curveInt = new CurveInteraction(this.canvas, curve, {
      curveClickAtCallback: (t) => {
        // points get priority over curve
        if (this.hoveredPoint === undefined) {
          this.splitCurveAt(curve, t);
        }
      }
    });

    const handlesInt = new CurveHandlesInteraction(this.canvas, curve, {
      fixedPoints: this.fixedPoints,
      // updateCallback: () => {
      //   this.draw();
      // },
      hoverPointCallback: (p) => {
        this.hoveredPoint = p;
      },
      pointRemoveCallback: (p) => this.cut(curve, p),
      endDragCallback: () => {
        this.validate();
        this.draw();
      }
    });

    this.curves.push(curve);
    this.curveInteractions.push(curveInt);
    this.handlesInteractions.push(handlesInt);
  }

  removeCurve(curve) {
    const int = this.curveInteractions.find((int) => int.curve === curve);

    if (int) {
      int.cleanup();
      this.curveInteractions.splice(this.curveInteractions.indexOf(int), 1);
      // todo; cleanup handleInteractions
    }

    const handlesInt = this.handlesInteractions.find((int) => int.curve === curve);

    if (handlesInt) {
      handlesInt.cleanup();
      this.handlesInteractions.splice(this.handlesInteractions.indexOf(handlesInt), 1);
    }

    this.curves.splice(this.curves.indexOf(curve), 1);
  }

  splitCurveAt(curve, t) {
    // console.log('TODO split at: ', curve, t);
    const split = curve.split(t);

    const oldfixed = [...this.fixedPoints];
    this.fixedPoints.length = 0;
    oldfixed.forEach((p) => {
      // fixed point not parse of split curve
      const idx = curve.points.indexOf(p);
      if (idx === -1) {
        this.fixedPoints.push(p);
      } else if (idx === 0) {
        this.fixedPoints.push(split.left.points[0]);
      } else if (idx === 3) {
        this.fixedPoints.push(split.right.points[3]);
      } else {
        console.warn('Unexpected fixed point index for split curve: ', idx);
      }
    });

    this.removeCurve(curve);
    this.addCurve(split.left);
    this.addCurve(split.right);
    this.validate();
    this.draw();
  }

  onWindowResize() {
    const curves = [...this.curves];
    this.cleanup();
    this.setup(curves);
  }

  _getMirrored(curves) {
    if (this.fixedPoints.length != 2 || this.fixedPoints[0].x !== this.fixedPoints[1].x) {
      console.warn("No two vertically-aligned fixed points, can't mirror");
      return [];
    }

    const xaxis = this.fixedPoints[0].x;
    const flip = (p) => ({
      x: xaxis - (p.x - xaxis),
      y: p.y});

    const mirrored = curves.map((c) => {
      const p = c.points.map((pp) => flip(pp));
      return new Bezier(
        p[0].x, p[0].y,
        p[1].x, p[1].y,
        p[2].x, p[2].y,
        p[3].x, p[3].y,
      )
    });

    return mirrored;
  }

  cut(curve, point) {
    let pointIndex;
    let removedPoint;

    if (point === curve.points[0] || point === curve.points[1]) { // start point removed
      pointIndex = 3;
      removedPoint = curve.points[0];
    } else if (point === curve.points[3] || point === curve.points[2]) { // start point removed
     pointIndex = 0;
     removedPoint = curve.points[3];
    } else {
      console.warn('Received unknown point in Curver.cut method');
      return;
    }

    const neighbour = this.curves.find((c) => c !== curve && c.points[pointIndex].x === removedPoint.x && c.points[pointIndex].y === removedPoint.y);

    if (neighbour === undefined) {
      console.warn('Could not find neighbouring curve');
      return;
    }

    // console.log('Removing: ', curve, ' and extending: ', neighbour);
    neighbour.points[pointIndex].x = curve.points[pointIndex].x;
    neighbour.points[pointIndex].y = curve.points[pointIndex].y;
    neighbour.update();

    this.removeCurve(curve);
    this.validate();
    this.draw();
  }
}